import React, {Component} from 'react';

export default class AboutUs extends Component {

  render() {
    return(
      <div className="section" id="om-oss">
        <div className="sectionContent">
          <h1>Hvem er vi?</h1>
          <p>
            Output er Informatikk sitt linjeforeningsband feteste (og eneste) linjeforeningsband.
            Vi ble stiftet tidlig 2018, og har gradvis bygd oss opp til  bli bandet vi er i dag gjennom hardt arbeid og store mengder pizza.
            Bandet har n av 8 medlemmer, som inkluderer en gitarist/vokalist, to vokalister, en bassist, gitarist, pianist, trommis og vr helt egne lydmann!
            <br/><br/>
            Vi har s langt spilt utallige konserter og vunnet flere priser enn vi kan telle, s hva som blir det neste er vanskelig  si.
          </p>
        </div>
      </div>
    )
  }
}



